*, :before, :after {
  box-sizing: border-box;
}

body {
  background-color: peru;
}

input[type="text"] {
  width: 400px;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #bdbdbd;
  border-radius: .45rem;
  margin-bottom: 50px;
  padding: .375rem .75rem;
  font-family: inherit;
  font-size: 22px;
  font-weight: 600;
  line-height: 1.72;
  transition: border-color .25s ease-in-out, box-shadow .25s ease-in-out;
  display: block;
}

.country-info {
  color: #063407;
  justify-content: center;
  font-size: 20px;
  display: grid;
}

.country-info__box {
  color: #231067;
  text-align: left;
  align-items: center;
}

.country-info__name-country {
  font-size: 24px;
  font-weight: 700;
}

.country-info__text {
  text-align: left;
  flex-direction: column;
  align-items: center;
}

.country-info__performance {
  text-align: center;
  font-size: 16px;
  font-weight: 700;
}

.country-list {
  column-count: 2;
